.App {
  text-align: center;  
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
  background-color: #F8333C;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    /*animation: App-logo-spin infinite 20s linear; */
  }
}

.App-header {
  background-image: url('./../public/Seinfeld_poster_serie0.jpg');
  background-color: #DBD5B5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color:#2B9EB3;
}

h1 {
  text-transform: uppercase;
  font-size: 40px;
  color:#44AF69;
}
form {
  text-transform: uppercase;
  font-size: 20px;
}
button {
  color: #FCAB10;
  font-size: 20px;
}

.App-link {
  color: #44AF69;
  /*61dafb;*/
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
